@import "https://fonts.googleapis.com/css2?family=Bitter:wght@600&display=swap";
body {
  color: var(--first-background-color);
  letter-spacing: .02em;
  background-color: #eeeee9;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.site-item {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.site-link {
  font-family: Bitter, serif;
  font-size: 26px;
}

.site-link:hover {
  color: #f8d24e;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.header {
  width: 100%;
  z-index: 1000;
  background-color: #282828;
  padding: 30px;
  position: fixed;
}

[class$="-container"] {
  max-width: 985px;
  margin: 0 auto;
}

.nav-list {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.nav-link {
  color: #fcfcfc;
  padding: 11px 18px;
  font-family: Bitter, serif;
  font-size: 16px;
  position: relative;
}

.nav-link:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: #caff34;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.nav-link:hover:after {
  transform: scaleX(1);
}

.hero {
  padding-top: 149px;
}

.hero-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.hero-title {
  font-family: Bitter, serif;
  font-size: 80px;
}

.hero-text {
  max-width: 421px;
  background-color: #caff34;
  margin-top: 42px;
  font-family: Bitter, serif;
  font-size: 24px;
}

.hero-img {
  width: 537px;
  height: 669px;
}

